const webFormOptionSelect = ({optionSelect, webFields}) => {

    // vem vybrany select
    // nabidni moznost jestli pridat do formu -> ano -> pridat do listu se stringem -> do stavu
    // ne -> zobraz druhy select s options + string -> vloz do listu se stringem -> do stavu
    const insertIntoForm = webFields.map(field => optionSelect === field.fieldName ? <p>
        Should be <strong>{field.fieldName}</strong> visible in form?<button>YES</button><button>NO</button>
    </p> : null)

    return (
        <>
        {insertIntoForm}
        </>
    )
}

export default webFormOptionSelect;